import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import logo from '../assets/logo.gif';
import { Toolbar } from './toolbar';
import { About } from './about';
import { Adopt } from './adopt';
import { OurPets } from './ourpets';
import { fetchAnimals, negotiate } from "../data/getdata";
//import { Sidebar } from './sidebar';
//import { fetchToken, fetchAnimals, negotiate } from "../data/getdata";


export default class HomeCompoent extends React.Component{
  token = null;
  constructor() {
    super();
    this.state = {
      animals: []
    };
  }

  componentDidMount() {
    this.fetchData();
  }

  async fetchData(pageNo = 1) {
    if (this.token == null) {
      //this.token = await fetchToken();
      this.token = await negotiate();
      //console.log("token", this.token);
    }
    if (this.token != null) {
      let data = await fetchAnimals(this.token.access_token);
      if (data != null) {
        if (data.status === 401) {
          this.token = null;
          await this.fetchData(pageNo);
          return;
        }
        else {
          let animals = [];
          let pagination = null;
          if (data.animals != null) {
            animals = data.animals;
            pagination = data.pagination;
          }

          if(pageNo===1){
            this.setState(
              {
                ...this.state,
                animals: []
              }
            );
          }

          this.setState(
            {
              ...this.state,
              animals: [
                ...this.state.animals,
                ...animals
              ]
            }
          );

          if(pagination.total_pages>pageNo){
            await this.fetchData(pageNo+1);
            return;
          }
        }
      }
    }
  }

  render(){
      return (
        <BrowserRouter>
          <div>
            <div className="toolbar">
              <div className="toolbar-logo">
                  <img alt="" src={logo} />
              </div>
              <Toolbar />
            </div>
            <div className="clear"></div>
            <div className="container">
              <Switch>
                <Route path="/adopt" component={Adopt} />
                <Route
                  path="/ourpets"
                  render={props => (
                    <OurPets {...props} animals={this.state.animals} />
                  )}
                />
                <Route component={About} />
              </Switch>
            </div>
          </div>
        </BrowserRouter>
      );
  }
}
