import React from 'react';
import cover from '../assets/cover2.jpg';

export const About = ()=>{
    return (
      <div className="about">
        <table>
            <tbody>
                <tr>
                    <td className="middle">
                        <img src={cover} alt="" />
                        <div className="center">
                            <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_blank">
                                <input type="hidden" name="cmd" value="_s-xclick" />
                                <input type="hidden" name="hosted_button_id" value="8589552" />
                                <input type="image" src="https://www.paypal.com/en_US/i/btn/btn_donateCC_LG.gif" border="0" name="submit" alt="PayPal - The safer, easier way to pay online!" />
                                <img alt="" border="0" src="https://www.paypal.com/en_US/i/scr/pixel.gif" width="1" height="1" ></img>
                            </form>
                        </div>
                        <div className="weare501">
                            <div className="text">
                                Cattitude Inc is a  501(c)(3) organization. Donations and 
                                contributions are deductable from Federal Income taxes.  
                                Gifts, bequests, legacies and transfers made to Cattitude 
                                Inc are deductible from Federal estate and gift taxes.
                            </div>
                        </div>
                    </td>
                    <td>
                        <div className="indent middle">
                            <center>
                                <h1>Our Mission</h1>
                            </center>
                            <div>
                                We are Cattitude, Inc a New Jersey Non - Profit Dog &amp; Cat
                                Rescue &amp; Adoption Corporation.Our mission is the rescue and
                                adoption of homeless and abandoned domestic cats &amp; dogs by
                                removing them from dangerous or otherwise inappropriate
                                environments and providing foster care, veterinary care,
                                socialization and any other appropriate intervention, as well
                                as: facilitating the adoption of cats &amp; dogs impounded in
                                local municipal shelters participation in and promotion of spay
                                / neuter programs designed to reduce the stray population
                                providing information about pet safety, pet training, pet birth
                                control, emergency care and otherwise promoting appropriate
                                treatment of cats &amp; dogs the prevention of cruelty to all
                                animals
                            </div>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
      </div>
    );
}
